.schedule__block .schedule__h2,
.schedule__block .schedule__h3 {
    font-family: "Istok Web", sans-serif
}

.primary-nav__logo .conference__logo img {
    background: rgba(255, 255, 255, .8);
    padding: 4px
}

.hero__content--conference {
    min-height: 60rem
}

.footer--conference .footer__logo img {
    width: 100%
}

.schedule__navigation.tab__navigation .tab__button {
    display: table-cell;
    float: none;
    max-width: 24rem;
    color: inherit;
    border: none;
    border-bottom: 3px solid transparent;
    background-color: #F2F2F2;
    position: relative
}

.schedule__entry:first-child:before,
.schedule__navigation.tab__navigation .tab__button.trigger__button--active:after {
    display: none
}

.schedule__navigation.tab__navigation .tab__button:before {
    content: "";
    background-color: #D6D6D6;
    height: 3rem;
    position: absolute;
    width: 1px;
    left: -1px;
    top: 1.5rem
}

.schedule__entry:after,
.schedule__entry:before {
    content: "";
    top: 0
}

.schedule__navigation.tab__navigation .tab__button.trigger__button--active {
    border-bottom: 3px solid #6AAEE6;
    color: inherit;
    background-color: #FFF
}

.schedule__block {
    border: 1px solid #D6D6D6;
    margin-top: 4rem
}

.schedule__block .schedule__h2 {
    color: #6AAEE6;
    font-weight: 700;
    margin-bottom: 0
}

.schedule__block .schedule__h3 {
    font-weight: 400;
    margin-top: 1rem
}

.schedule__set {
    margin-top: 4rem
}

.schedule__entry {
    position: relative;
    overflow: hidden
}

.schedule__entry:after {
    background-color: #ececec;
    position: absolute;
    height: 60rem;
    width: 4px;
    left: 3.6rem;
    bottom: 0
}

.schedule__entry:before {
    background-color: #D6D6D6;
    position: absolute;
    height: 1px;
    width: calc(100% - 16rem);
    left: 8rem
}

.schedule__entry:first-child:after {
    box-shadow: inset 0 24px 24px -12px #FFF
}

.schedule__entry:last-child:after {
    top: initial;
    bottom: 0;
    box-shadow: inset 0 -24px 24px -12px #FFF
}

.schedule__entry .schedule-entry__icon {
    position: absolute;
    left: 2rem;
    font-size: 4rem;
    top: 2rem;
    height: 5rem;
    padding-top: .5rem;
    text-align: center;
    color: #6AAEE6;
    z-index: 1;
    background-color: #FFF
}

.schedule__entry .schedule__time h3 {
    font-weight: 400;
    text-align: left;
    padding-left: 6.5rem
}

.schedule__entry .schedule__content {
    margin-bottom: 2rem
}

.schedule__entry .schedule__content p {
    margin: 0
}

@media (max-width:991px) {
    .schedule__entry .schedule__content,
    .schedule__entry .schedule__time {
        z-index: 1
    }
    .schedule__entry:after {
        display: none
    }
}


